<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    <ul>
      <li>
        <a
          href="https://vuejs.org"
          target="_blank"
        >
          Core Docs
        </a>
      </li>
      <li>
        <a
          href="https://forum.vuejs.org"
          target="_blank"
        >
          Forum
        </a>
      </li>
      <li>
        <a
          href="https://chat.vuejs.org"
          target="_blank"
        >
          Community Chat
        </a>
      </li>
      <li>
        <a
          href="https://twitter.com/vuejs"
          target="_blank"
        >
          Twitter
        </a>
      </li>
      <br>
      <li>
        <a
          href="http://vuejs-templates.github.io/webpack/"
          target="_blank"
        >
          Docs for This Template
        </a>
      </li>
    </ul>
    <h2>Ecosystem</h2>
    <ul>
      <li>
        <a
          href="http://router.vuejs.org/"
          target="_blank"
        >
          vue-router
        </a>
      </li>
      <li>
        <a
          href="http://vuex.vuejs.org/"
          target="_blank"
        >
          vuex
        </a>
      </li>
      <li>
        <a
          href="http://vue-loader.vuejs.org/"
          target="_blank"
        >
          vue-loader
        </a>
      </li>
      <li>
        <a
          href="https://github.com/vuejs/awesome-vue"
          target="_blank"
        >
          awesome-vue
        </a>
      </li>
    </ul>
    <div><span @click="invokePushItems(item)">加入购物车</span></div>
    <div>
        <ul>
            <li v-for="(val,index) in arrList" :key="index">
                <h5>{{val.productName}}</h5>
                 <p>{{val.price}}</p>
            </li>
        </ul>
    </div>
    <div>
      <span @click="toConsole">去控制台</span>
      <router-link to="index"><span >去首页</span></router-link>
    </div>
    <div v-if="isShow"><h1>显示底部</h1></div>
  </div>
</template>

<script>
// import {mapState} from 'vuex'
import {mapGetters, mapActions} from 'vuex'
// import {mapState, mapGetters, mapActions} from 'vuex'

export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      number: 8,
      c: 'deep watcher',
      item: {
        id: '01',
        productName: '苹果',
        price: '1.6元/斤'
      }
    }
  },
  computed: {
    // isShow () {
    //   // console.log(this.$store)
    //   // return this.$store.state.showFooter
    //   return this.$store.getters.isShow
    // }

    // 这里的...是超引用，ES6的语法，意思是state里有多少属性值我可以在这里放多少属性值
    // ...mapState({
    //   // 注意这些与上面的区别就是state.footerStatus,里面定义的showFooter是指footerStatus.js里state的showFooter
    //   // isShow: state => state.footerStatus.showFooter
    //   isShow (state) {
    //     console.log(state)
    //     return state.footerStatus.showFooter
    //   }
    // })

    // 你也可以用下面的mapGetters来获取isShow的值，貌似下面的更简洁
    // footerStatus指的是modules文件夹下的footerStatus.js模块
    ...mapGetters('footerStatus', {
    // 第一个isShow是我自定义的只要对应template里v-if="isShow"就行，第二个isShow是对应的footerStatus.js里的getters里的isShow
      isShow: 'isShow'
    }),
    ...mapGetters('collection', { // 用mapGetters来获取collection.js里面的getters
      arrList: 'renderCollects'
    })
  },
  watch: {
    // 监控msg变量变化的时候，自动执行此函数
    msg: function (val, oldVal) {
      console.log('msg---new: %s, old: %s', val, oldVal)
    },
    // 深度 watcher
    c: {
      handler: function (val, oldVal) { console.log('c---new: %s, old: %s', val, oldVal) },
      deep: true
    },
    $route (to, from) { // 跳转组件页面后，监听路由参数中对应的当前页面以及上一个页面
      // console.log(to)
      // console.log(from)
      if (to.name === 'book' || to.name === 'my') { // to.name来获取当前所显示的页面，从而控制该显示或隐藏footerBar组件
        // this.$store.dispatch('showFooter') // 利用派发全局state.showFooter的值来控制
        this.$store.dispatch('footerStatus/showFooter')// 这里改为'footerStatus/showFooter',意思是指footerStatus.js里actions里的showFooter方法
      } else {
        // this.$store.dispatch('hideFooter')
        this.$store.dispatch('footerStatus/hideFooter')
      }
    }
  },
  methods: {
    ...mapActions('collection', [ // collection是指modules文件夹下的collection.js
      'invokePushItems'// collection.js文件中的actions里的方法，在上面的@click中执行并传入实参
    ]),
    toConsole () { location.href = 'console.html' }
  },
  // 在实例初始化之后，数据观测(data observer) 和 event/watcher 事件配置之前被调用
  beforeCreate: function () {
    console.log('beforeCreate 钩子执行...')
    console.log(this.number)
  },
  // 实例已经创建完成之后被调用。
  // 在这一步，实例已完成以下的配置：数据观测(data observer)，属性和方法的运算， watch/event 事件回调。
  // 然而，挂载阶段还没开始，$el 属性目前不可见
  cteated: function () {
    console.log('cteated 钩子执行...')
    console.log(this.number)
  },
  // 在挂载开始之前被调用：相关的 render 函数首次被调用
  beforeMount: function () {
    console.log('beforeMount 钩子执行...')
    console.log(this.number)
  },
  // el 被新创建的 vm.$el 替换，并挂载到实例上去之后调用该钩子。
  // 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$el 也在文档内
  mounted: function () {
    console.log('mounted 钩子执行...')
    console.log(this.number)
  },
  // 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
  // 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程
  beforeUpdate: function () {
    console.log('beforeUpdate 钩子执行...')
    console.log(this.number)
  },
  // 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
  // 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
  // 然而在大多数情况下，你应该避免在此期间更改状态，因为这可能会导致更新无限循环。
  // 该钩子在服务器端渲染期间不被调用。
  updated: function () {
    console.log('updated 钩子执行...')
    console.log(this.number)
  },
  // 实例销毁之前调用。在这一步，实例仍然完全可用
  beforeDestroy: function () {
    console.log('beforeDestroy 钩子执行...')
    console.log(this.number)
  },
  // Vue 实例销毁后调用。
  // 调用后，Vue 实例指示的所有东西都会解绑定，所有的事件监听器会被移除，所有的子实例也会被销毁。
  // 该钩子在服务器端渲染期间不被调用。
  destroyed: function () {
    console.log('destroyed 钩子执行...')
    console.log(this.number)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



